





















import { Component, Vue } from 'vue-property-decorator';

// Vue elements
import GameVue from './components/Game.vue';
import IntroVue from './components/Intro.vue';
import PuzzleTestVue from './components/puzzles/PuzzleTest.vue';

// Engine elements
import Game from './engine/Game';
import createGame from './games/game';
import createTest from './games/test';

import Preloader from './utils/preloader';
import sleep from './utils/sleep';

@Component({
    components: {
        GameVue,
        IntroVue,
        PuzzleTestVue
    },
})
export default class App extends Vue {

    loaded = false;
    game: Game|null = null;

    mounted(){

        this.waitLoad();

        // TODO: rm
        //this.game = createGame();

    }

    async waitLoad(){

        await Preloader.preloadFolder('fonts');
        await Preloader.preloadFolder('base');
        await Preloader.preloadFolder('puzzles');
        this.loaded = true;

    }

    startGame(){

        this.game = createGame();

    }

    tests(){
        this.game = createTest();
    }

}

