
import Game from '../engine/Game';
import Room from '../engine/Room';
import { Puzzle } from '../engine/Puzzle';
import { ActionRoom, ActionMsg } from '../engine/Action';
import Image from '../engine/Image';

export default function createGame(){


    const game = new Game();

    const img1 = new Image('scape-test/book-open.png','escape-test/book-closed.jpg');
    img1.zoom = true;

    const r0 = new Room("Vestbulo", 'open');
    r0.add(`
    Ests en el vestbulo. Ante t hay una puerta, posas tu mano en el pomo
    pensando que intrincados acertijos debers resolver cuando de pronto
    te das cuenta que la puerta ya est abierta.
    Un poco decepcionado prosigues con tu aventura.
    `);
    r0.add(`
    Utiliza el men de lugares para moverte entre localizaciones.
    `);
    // r0.add(img1);


    const rA = new Room("Saln", 'open');
    const rB = new Room("Habitacin final");


    rA.add(`
        Ests en el saln, una habitacin sin ninun tipo de mobiliario.
        En el suelo pareces distinguir un libro.
        Adems hay una puerta con un candado numrico que te impide el paso
        a la habitacin final.
    `);

    rA.add(img1);

    const rA_p1 = new Puzzle('combination', {code:'10-5-15'});
    const rA_p1_a1 = new ActionRoom(rB, 'open');
    rA_p1.add(rA_p1_a1);
    rA_p1.add(new ActionMsg(`
        Muy bien, tus habilidades investigadores superiores te han permitido solucionar la prueba.
        Ahora puedes entrar en la habitacin final.
        
    `.trim()));
    rA.add(rA_p1);
    r0.add(rA_p1);

    rB.add("Triunfante entras en la habitacin final. Has ganado.")


    game.add(r0);
    game.add(rA);
    game.add(rB);

    return game;

}