













































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import {ClientV1 as Multiplayer} from '@javfres/escaperoom-multiplayer';

@Component
export default class IntroVue extends Vue {

    join_url: string = '';
    multiplayer: Multiplayer|null = null;

    user_list: string[] = [];
    my_name = '';

    mounted(){
        
        const params = new URLSearchParams(window.location.search);
        const game_id = params.get('join');
        console.log(game_id);
        if(game_id) this.join_game(game_id);

    }

    async join_game(game_id: string){
        const multiplayer = new Multiplayer('https://multiplayer.javilabs.es');
        await multiplayer.join(game_id);
        this.multiplayer = multiplayer;

        setInterval(() => this.update_game(), 5000);
    }

    update_game(){

        if(!this.multiplayer) return;
        this.user_list = this.multiplayer.user_list.map((x,i) => {
            if(!this.multiplayer) return 'X';
            return x + " " + ( this.multiplayer.user_data[i].name  ) ;

        });

    }

    single_game(){
        this.$emit('start');
    }


    @Watch('my_name')
    change_name(name:string){

        if(!this.multiplayer) return;

        this.multiplayer.userData({name});

    }


    async create_game(){
        

        const multiplayer = new Multiplayer('https://multiplayer.javilabs.es');
        await multiplayer.create();
        this.multiplayer = multiplayer;

        this.join_url = window.location.href.split('?')[0];
        this.join_url += '?join=' + this.multiplayer.game_id;

        setInterval(() => this.update_game(), 5000);

    }


}

