import Preloader from '@/utils/preloader';

const assets = require.context(
    "../assets",  // context folder
    true,         // include subdirectories
    /.*/,         // RegExp
);

function getAsset(name:string){
    return assets('./' + name);
}


export default class Image {

    assets: string[] = [];
    url: string;
    url_prev: string|null = null;
    zoom: boolean = false;

    constructor(url: string, url_prev: string|null = null){
        
        this.url = getAsset(url);
        this.assets.push(url);

        if(url_prev){
            this.url_prev = getAsset(url_prev);
            this.assets.push(url_prev);
        }

    }

    async preload(){

        const waits = this.assets.map(a => Preloader.preload(a));
        await Promise.all(waits);

    }

}